<template lang="pug">
    div.todo__app-field
        input.todo__app-field-input(
            v-model="text"
            type='text'
            placeholder="Введите текст задачи..."
            @keyup.enter="addTask"
        )
        button.todo__app-field-button(
            @click="addTask"
        )
            svg.todo__app-field-plus(height="20px" viewBox="0 0 469.33333 469.33333" width="20px" xmlns="http://www.w3.org/2000/svg")
                path(d="m437.332031 192h-160v-160c0-17.664062-14.335937-32-32-32h-21.332031c-17.664062 0-32 14.335938-32 32v160h-160c-17.664062 0-32 14.335938-32 32v21.332031c0 17.664063 14.335938 32 32 32h160v160c0 17.664063 14.335938 32 32 32h21.332031c17.664063 0 32-14.335937 32-32v-160h160c17.664063 0 32-14.335937 32-32v-21.332031c0-17.664062-14.335937-32-32-32zm0 0")

</template>

<script>
export default {
    data: () => ({
        text: ""
    }),
    methods: {
        addTask() {
            if(this.text) {
                this.$emit("onAddTask", this.text)
                this.text = ""
            }
        }
    }
}
</script>